import Image from "next/image";
import Link from "next/link";
import { FaHome, FaArrowLeft } from "react-icons/fa";
import BackButton from "./BackButton";

export default function NotFound() {
  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 via-black to-gray-800 flex items-center justify-center px-4">
      <div className="max-w-md w-full text-center">
        {/* Logo Brocode */}
        <div className="mb-8 flex justify-center">
          <div className="relative">
            <Image
              src="/assets/logo.png"
              alt="Brocode Logo"
              width={120}
              height={120}
              className="object-contain"
            />
            <div className="absolute -inset-4 bg-[#FDFB03] rounded-full opacity-20 animate-pulse"></div>
          </div>
        </div>

        {/* 404 Text */}
        <div className="mb-8">
          <h1 className="text-8xl font-bold text-[#FDFB03] mb-4 animate-bounce">
            404
          </h1>
          <h2 className="text-2xl font-bold text-white mb-2">
            Halaman Tidak Ditemukan
          </h2>
          <p className="text-gray-400 text-lg leading-relaxed">
            Maaf, halaman yang Anda cari tidak dapat ditemukan. Mungkin halaman
            telah dipindahkan atau tidak pernah ada.
          </p>
        </div>

        {/* Decorative Elements */}
        <div className="mb-8 flex justify-center space-x-2">
          <div className="w-3 h-3 bg-[#FDFB03] rounded-full animate-pulse"></div>
          <div className="w-3 h-3 bg-[#FDFB03] rounded-full animate-pulse"></div>
          <div className="w-3 h-3 bg-[#FDFB03] rounded-full animate-pulse"></div>
        </div>

        {/* Action Buttons */}
        <div className="space-y-4">
          <Link href="/">
            <button className="w-full bg-[#FDFB03] hover:bg-yellow-400 text-black font-semibold py-4 px-6 rounded-xl transition-all duration-300 transform hover:scale-105 flex items-center justify-center space-x-3 shadow-lg">
              <FaHome size={20} />
              <span>Kembali ke Beranda</span>
            </button>
          </Link>

          <BackButton />
        </div>

        {/* Footer Text */}
        <div className="mt-8 pt-8 border-t border-gray-700">
          <p className="text-gray-500 text-sm">Brocode Aceh Barbershop</p>
          <p className="text-gray-600 text-xs mt-1">
            Jika masalah berlanjut, silakan hubungi administrator
          </p>
        </div>
      </div>

      {/* Background Decorations */}
      <div className="fixed inset-0 overflow-hidden pointer-events-none">
        <div className="absolute top-1/4 left-1/4 w-32 h-32 bg-[#FDFB03] rounded-full opacity-5 animate-pulse"></div>
        <div className="absolute top-3/4 right-1/4 w-24 h-24 bg-[#FDFB03] rounded-full opacity-5 animate-pulse"></div>
        <div className="absolute top-1/2 left-1/3 w-16 h-16 bg-[#FDFB03] rounded-full opacity-5 animate-pulse"></div>
      </div>
    </div>
  );
}
